import {mapState} from 'vuex';
export default {
	data() {
		return {
			hospitalList:[],
			 filterList:[
				 {
				 identify:"olevel",
				 name:"级别",
				 itemList:[
					 {
						 checked:true,
						  dkey: "",
						  dvalues: "不限"
					 }			 
				 ]
				},
				{
				 identify:"orgType",
				 name:"类型",
				 itemList:[
					 {
						 checked:true,
						  dkey: "",
						  dvalue: "不限"
					 }
				 ]
				}
			 ],								
			areaList:[
				{
					dkey:"",
					dvalue:"全市",
					checked:true
				},		
			],
			factorCur:0,
			factorTitle:{
				area:"全市",
				gap:"就近",
				shaixuan:{
					olevel:"不限",
					orgType:"不限"
				}
			},
			pagenum:1,
			loadingType:"more",
			postDataObj:{
				gpsLongitude:"",
				gpsLatitude:"",
				county:"",
				orgType:"",
				olevel:"",
				onameKey:"",
				hotOrderTag:"",
				userId:0,
				oproperty:"",
				orgPertainType:"",
				orderPoolType:"9"
			},
			hasPros:false
			
		}
	},
	onLoad(){
		
		uni.getLocation({
		    type: 'wgs84',
		    success:(res)=>{
				this.postDataObj.gpsLongitude= res.longitude;
				this.postDataObj.gpsLatitude=res.latitude;
				this.getData("refresh");
		    },
			fail:(err)=>{
				console.log(err);
			},
			
		});
		this.getData();

		wx.login({
			// provider:"weixin",
			success:(res)=>{
				console.log(res)
				if(res.code){
					wx.request({
						url:'https://jklz.jkluzhou.cn/healthy/getOpenId.do',
						method: 'POST',									
						data:{
							code:res.code
						},
						success:(openInfo)=>{
							console.log(openInfo);
						},
						fail:(err)=>{
							console.log(res.code)
							console.log(err);
						}
					})
		
				}
				// uni.getUserInfo({
				//       provider: 'weixin',
				//       success:  (infoRes)=>{
				//         console.log(infoRes);
				//       }
				//     });
			},
			fail:(err)=>{
				console.log(err)
			},
			
		})

	},
	methods: {
		searchClick(){
			uni.navigateTo({
				url: '/pages/appointment/search/search'
			});
		},
		onFactorClick(e){
			if(e==this.factorCur){
				this.factorCur=0;
				return;
			}				
			this.factorCur=e;
			if(!this.hasPros){
				this.getHospitalPros()
			}
		},
		fileredClick(index,subindex,identify){				
			this.filterList[index].itemList.forEach((v)=>{
				v.checked=false
			})				
			this.filterList[index].itemList[subindex].checked=true;
			let field="";
			field=index==0?"dvalues":"dvalue";
			this.factorTitle.shaixuan[identify]=this.filterList[index].itemList[subindex][field];
		},
		clearFalered(){
			for(let item of this.filterList){
				item.itemList.forEach((v)=>{						
						v.checked=false										
				})
				item.itemList[0].checked=true;
			}
		},
		sureFalered(){				
			for(let item of this.filterList){					
				item.itemList.forEach((v)=>{						
					if(v.checked==true){
						this.postDataObj[item.identify]=v.dkey;
					}									
				})					
			}
			this.getData("refresh");
			this.onFactorClick(0);
		},
		areaClick(index,value){
			this.areaList.forEach((item)=>{
				item.checked=false;
			})
			this.areaList[index].checked=true;
			this.postDataObj.orgPertainType=value.dkey;
			this.factorTitle.area=value.dvalue;
			this.getData("refresh");
			this.onFactorClick(0);
		},
		hospitalClick(orgUuid){
			let paramsUrl="?orgUuid="+orgUuid;
			uni.navigateTo({
				url: '/pages/appointment/hospital-dept/hospital-dept'+paramsUrl
			});
		},
		getHospitalPros(){
			this.hasPros=true;			
			this.$globalMethod.ds_api({
				method:"m=K001&token=SemXlsuV0Ko2Ugrp&pagesize=20&pagenum=1",
				loadmsg:"医院区域查询",
				showLoading:false,
				data:{
					"dcode":"DICT_ORG_PERTAIN_TYPE",
					"dkey":"",
					"dvalue":""
				},
				success:(res)=>{
					if(!res || res.data.data.length==0){
						return
					}
					let list=res.data.data;
					list.forEach((item)=>{
						this.$set(item,"checked",false);
						this.areaList.push(item);
					})	
				}					
			})
			this.$globalMethod.ds_api({
				method:"m=K001&token=SemXlsuV0Ko2Ugrp&pagesize=20&pagenum=1",
				loadmsg:"医院类型查询",
				showLoading:false,
				data:{
					"dcode":"DICT_ORG_TYPE",
					"dkey":"",
					"dvalue":""
				},
				success:(res)=>{
					if(!res || res.data.data.length==0){
						return
					}
					let list=res.data.data;
					list.forEach((item)=>{
						this.$set(item,"checked",false);
						this.filterList[1].itemList.push(item)
					})
				}					
			})
			this.$globalMethod.ds_api({
				method:"m=K002&token=SemXlsuV0Ko2Ugrp&pagesize=20&pagenum=1",
				loadmsg:"医院级别查询",
				showLoading:false,
				data:{},
				success:(res)=>{
					if(!res || res.data.data.length==0){
						return
					}
					let list=res.data.data;
					list.forEach((item)=>{
						this.$set(item,"checked",false);
						this.filterList[0].itemList.push(item)
					})
				}					
			})
			
			
		},
		getData(type="add"){
			if (this.loadingType == 'loading' || this.loadingType=="nomore") {
				//防止重复加载
				return;
			}
			//下拉刷新加载时页码为1
			if (type == 'refresh') {
				this.hospitalList = [];
				this.pagenum = 1;
			}
			this.loadingType = 'loading';
			
			this.$globalMethod.ds_api({
				method:"m=B001&token=SemXlsuV0Ko2Ugrp&pagesize=20&pagenum="+this.pagenum,
				loadmsg:"医院列表",
				data:this.postDataObj,
				success:(res)=>{
					if(!res || !res.data.data || res.data.data.length==0){
						this.loadingType = 'nomore';
						return
					}
					
					let list=res.data.data;
					this.loadingType=list.length>=20?'more' : 'nomore';
					list.forEach((item)=>{
						this.hospitalList.push(item);
					})
					this.pagenum++;
				},
				fail:(err)=>{						
					uni.showToast({
						title:"获取数据失败",
						icon:"none"
					})						
				}
				
			})
			
		},
		stopPrevent(){}
	},
	onReachBottom() {
		if (this.loadingType == 'nomore') {
			return;
		}
		this.getData();
	},
	onPullDownRefresh() {
		this.getData('refresh');
	},
	computed: {
		...mapState(['$status']),
	},
}
